import React from 'react';
import { useContent } from '../../userContext';
// import styles from './MainImg.module.css';

const MainImg = ({ pokemon, styles }) => {
  const { capitalizeFirstLetter } = useContent();

  return (
    <div>
      <div className={`${styles.wrapper} ${styles.whapperColor}`}>
        <div className={styles.bg}>
          <img
            src={
              pokemon.sprites.other.home.front_default
                ? pokemon.sprites.other.home.front_default
                : pokemon['sprites']['other']['official-artwork'][
                    'front_default'
                  ]
            }
            alt=""
          />
        </div>
      </div>
      <div className={`${styles.whapperColor} ${styles.pBg}`}>
        <p>
          {capitalizeFirstLetter(pokemon.types[0].type.name)} Pokmon. Length{' '}
          {pokemon.stats.length}, Weight: {pokemon.weight} kg
        </p>
      </div>
    </div>
  );
};

export default MainImg;
