import { useEffect, useState } from 'react';
import { POKEMON_TYPE } from '../../Api';
import useFetch from '../../Hooks/useFetch';
import ImgEvolution from './ImgEvolution';
import styles from './Header.module.css';
import { useContent } from '../../userContext';

const Header = ({ name, headerUrl, pokemon, setBasic }) => {
  const { capitalizeFirstLetter } = useContent();
  const [header, setHeader] = useState(null);
  const [imgHeader, setImgHeader] = useState(null);

  const { request } = useFetch();
  useEffect(() => {
    async function getHeader() {
      const { option } = await POKEMON_TYPE();
      const { json } = await request(headerUrl, option);
      setHeader(json);
    }
    if (headerUrl) getHeader();
  }, [headerUrl, request]);

  useEffect(() => {
    imgHeader ? setBasic(true) : setBasic(false);
    if (header && header.evolves_from_species) {
      setImgHeader(header.evolves_from_species.name);
    }
  }, [header, imgHeader, setBasic]);

  // if (header) console.log(basic);

  return (
    <>
      {header && (
        <div
          className={`${imgHeader ? ' ' : styles.basic} ${styles.container}`}
        >
          <div className={styles.wrapper}>
            <div className={styles.corner}></div>
            <ImgEvolution evoImg={imgHeader} />
          </div>
          <div className={styles.info}>
            <h1>{capitalizeFirstLetter(name)}</h1>
            {imgHeader ? (
              <p>Evolves From: {capitalizeFirstLetter(imgHeader)}</p>
            ) : (
              ''
            )}
          </div>
          <div className={styles.hp}>
            <h3>
              <span>HP {+pokemon.stats[0].base_stat * 2}</span>
            </h3>
          </div>
        </div>
      )}
    </>
  );
};

export default Header;
