import { useEffect, useState } from 'react';
import styles from './Card.module.css';
import style from './Ability.module.css';
import imgStyles from './MainImg.module.css';
import Ability from './Ability';
import Header from './Header';
import MainImg from './MainImg';

const Card = ({ pokemon }) => {
  const [abilityUrl, setAbilityUrl] = useState(null);
  const [headerUrl, setHeaderUrl] = useState(null);
  const [basic, setBasic] = useState(null);

  useEffect(() => {
    pokemon &&
      pokemon.abilities.length !== 0 &&
      setAbilityUrl(pokemon.abilities[0].ability.url);
    pokemon && setHeaderUrl(pokemon.species.url);
  }, [pokemon]);

  return (
    <>
      {pokemon && (
        <div className={styles.container}>
          <div className={styles.card}>
            <Header
              pokemon={pokemon}
              name={pokemon.name}
              headerUrl={headerUrl}
              setBasic={setBasic}
              basic={basic}
            />
            <MainImg pokemon={pokemon} styles={imgStyles} />
            {abilityUrl ? (
              <Ability url={abilityUrl} basic={basic} />
            ) : (
              <div className={`${basic && style.basic} ${style.container}`}>
                <h2>Ability: None</h2>
                <div className={style.abilities}>
                  <p>
                    <span>Effect: </span>
                    None
                  </p>
                </div>
                <br />
                <p>
                  <span> Short Effect:</span> None
                </p>
              </div>
            )}
          </div>
        </div>
      )}
    </>
  );
};

export default Card;
