import React, { useEffect, useState } from 'react';
import { POKEMON_TYPE } from '../../Api';
import useFetch from '../../Hooks/useFetch';
import { useContent } from '../../userContext';
import styles from './Ability.module.css';

const Ability = ({ url, basic }) => {
  const { capitalizeFirstLetter } = useContent();
  const [ability, setAbility] = useState(null);
  const [abilityEffect, setAbilityEffect] = useState(null);
  const [abilityShortEffect, setAbilityShortEffect] = useState('');

  const { request } = useFetch();

  useEffect(() => {
    async function getHeader() {
      const { option } = await POKEMON_TYPE();
      const { json } = await request(url, option);
      setAbility(json);
    }
    getHeader();
  }, [request, url]);

  useEffect(() => {
    ability && setAbilityEffect('This Ability does not have any Effects');
    if (ability && ability.effect_entries.length !== 0) {
      if (ability.effect_entries[0].language.name !== 'en') {
        setAbilityEffect(ability.effect_entries[1].effect);
        setAbilityShortEffect(ability.effect_entries[1].short_effect);
      }
      if (ability.effect_entries[0].language.name === 'en') {
        setAbilityEffect(ability.effect_entries[0].effect);
        setAbilityShortEffect(ability.effect_entries[0].short_effect);
      }
    }
  }, [ability]);

  // ability && console.log(ability);
  // ability && console.log(abilityEffect);

  return (
    <>
      {ability && (
        <div className={`${basic && styles.basic} ${styles.container}`}>
          <h2>Ability: {capitalizeFirstLetter(ability.name)}</h2>
          <div className={styles.abilities}>
            <p>
              <span>Effect: </span>
              {abilityEffect}
            </p>
          </div>
          <br />
          <p>
            <span> Short Effect:</span> {abilityShortEffect}
          </p>
        </div>
      )}
    </>
  );
};

export default Ability;
