export const API_URL = 'https://pokeapi.co/api/v2';

export function POKEMON_TYPE() {
  return {
    url: `${API_URL}/type/`,
    options: {
      method: 'GET',
    },
  };
}

export function POKEMON_GET(name) {
  return {
    url: `${API_URL}/pokemon/${name}`,
    retoptions: {
      method: 'GET',
    },
  };
}

export function POKEMON_LIMIT(offset = 0, limit = 10) {
  return {
    url: `${API_URL}/pokemon?offset=${offset}&limit=${limit}`,
    retoptions: {
      method: 'GET',
    },
  };
}
