import { useEffect, useState } from 'react';
import { POKEMON_TYPE } from '../Api';
import useFetch from '../Hooks/useFetch';
import { useContent } from '../userContext';
import styles from './Menu.module.css';

const Test = ({
  setValue,
  setName,
  choose,
  setChoose,
  setSelected,
  isActive,
  setIsActive,
}) => {
  const { capitalizeFirstLetter } = useContent();

  const [type, setType] = useState(null);
  const { request } = useFetch();

  useEffect(() => {
    async function getPokemonType() {
      const { url, option } = await POKEMON_TYPE();
      const { json } = await request(url, option);
      setType(json['results']);
    }
    getPokemonType();
  }, [request]);

  return (
    <div className={styles.dropdown}>
      <div className={styles.dropdownBtn}>
        {choose ? choose : 'Search pokemon by Type'}
        {choose && (
          <span
            className={styles.close}
            onClick={() => {
              setChoose(null);
              setIsActive(false);
            }}
          >
            x
          </span>
        )}
        <span
          className={styles.downButton}
          onClick={(e) => setIsActive(!isActive)}
        ></span>
      </div>

      {isActive && (
        <div className={styles.dropdownContent}>
          {type.map((option) => (
            <div
              key={option.name}
              className={styles.dropdownItem}
              onClick={(e) => {
                setSelected(option.url);
                setIsActive(false);
                setChoose(capitalizeFirstLetter(option.name));
                setName(null);
                setValue('');
              }}
            >
              {capitalizeFirstLetter(option.name)}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Test;
