import { useEffect, useState } from 'react';
import { POKEMON_TYPE } from '../Api';
import useFetch from '../Hooks/useFetch';
import CardSample from './Card/CardSample/CardSample';
import styles from './CardsDisplay.module.css';
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';

const CardsDisplay = ({ name, choose, selected }) => {
  const [pokemonsType, setPokemonsType] = useState(null);
  const [pokemons, setPokemons] = useState([]);
  const [leftScroll, setLeftScroll] = useState(false);
  // const [rightScroll, setRightScroll] = useState(false);

  // SCROLL BAR FUNCTION

  const slideLeft = () => {
    var slider = document.getElementById('scroll');
    slider.scrollLeft = slider.scrollLeft - 660;
    slider.scrollLeft < 900 && setLeftScroll(false);
  };

  const slideRight = () => {
    var slider = document.getElementById('scroll');
    slider.scrollLeft = slider.scrollLeft + 660;
    setLeftScroll(true);
  };

  // *************************************************

  const { request } = useFetch();
  useEffect(() => {
    async function getPokemon() {
      const { option } = await POKEMON_TYPE();
      const { json } = await request(selected, option);
      setPokemonsType(json.pokemon);
    }
    if (selected) getPokemon();
  }, [request, selected]);

  useEffect(() => {
    if (pokemonsType) {
      setPokemons('');
      pokemonsType.map((e) =>
        setPokemons((pokemon) => [...pokemon, e.pokemon.name]),
      );
    }
  }, [pokemonsType]);
  // pokemons && console.log(scroll.current);

  return (
    <div className={styles.wrapper}>
      {choose && leftScroll && (
        <div
          style={{
            borderRadius: ' 15px 0  0 15px ',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            width: '35px',
            height: '100%',
            background: 'black',
            position: 'absolute',
            zIndex: '5',
            opacity: '.3',
          }}
        >
          <KeyboardArrowLeftIcon
            onClick={slideLeft}
            sx={{ color: 'white', scale: '3', cursor: 'pointer' }}
          />
        </div>
      )}
      {
        <div className={styles.container} id="scroll">
          {pokemons &&
            !name &&
            pokemons.map((e) => <CardSample key={e} name={e} />)}
          {name && <CardSample name={name} />}
        </div>
      }
      {choose && (
        <div
          style={{
            borderRadius: ' 0 15px 15px 0',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            width: '35px',
            height: '100%',
            background: 'black',
            position: 'absolute',
            zIndex: '5',
            top: '0',
            right: '-2px',
            opacity: '.3',
          }}
        >
          <KeyboardArrowRightIcon
            onClick={slideRight}
            sx={{ color: 'white', scale: '3', cursor: 'pointer' }}
          />
        </div>
      )}
    </div>
  );
};

export default CardsDisplay;
