import { useEffect, useState } from 'react';
import { POKEMON_GET } from '../../../Api';
import useFetch from '../../../Hooks/useFetch';
import { useContent } from '../../../userContext';
import MainImg from '../MainImg';
import styles from './CardSample.module.css';
import SearchOutlinedIcon from '@mui/icons-material/SearchOutlined';
import CardModal from './CardModal';

const CardSample = ({ name }) => {
  const { capitalizeFirstLetter, pokedeck, setPokedeck } = useContent();
  const [modal, setModal] = useState(false);
  const [pokemon, setPokemon] = useState(null);

  const [background, setBackground] = useState('');
  useEffect(() => {
    function changingBackground() {
      switch (pokemon.types[0].type.name) {
        case 'dark':
          setBackground('darkness.png');
          break;
        case 'dragon':
          setBackground('dragon.png');
          break;
        case 'fighting':
          setBackground('fighting.png');
          break;
        case 'fire':
          setBackground('fire.png');
          break;
        case 'grass':
          setBackground('grass.png');
          break;
        case 'electric':
          setBackground('lightning.png');
          break;
        case 'steel':
          setBackground('metal.png');
          break;
        case 'psychic':
          setBackground('psychic.png');
          break;
        case 'water':
          setBackground('water.png');
          break;

        default:
          setBackground('holografic.jpg');
      }
    }
    pokemon && changingBackground();
  }, [pokemon]);

  const { request } = useFetch();
  useEffect(() => {
    async function getPokemon() {
      const { url, option } = await POKEMON_GET(name);
      const { json } = await request(url, option);
      setPokemon(json);
    }
    if (name) getPokemon();
  }, [name, request]);

  function handleModal() {
    setModal(!modal);
    console.log(modal);
  }

  function changeFav() {
    if (!pokedeck.includes(name) && pokedeck.length <= 6) {
      setPokedeck([...pokedeck, name]);
    } else if (pokedeck.includes(name)) {
      let newDeck = pokedeck.filter((value) => value !== name);
      setPokedeck([...newDeck]);
    } else {
      alert('Your Pokedeck if full');
    }
  }

  return (
    <>
      {modal && (
        <CardModal pokemon={pokemon} setModal={setModal} modal={modal} />
      )}
      <div className={styles.container}>
        <div className={styles.header}>
          <p>Hp {pokemon && +pokemon.stats[0].base_stat * 2}</p>
          <div onClick={changeFav} className={styles.favorite}>
            {pokedeck.includes(name) ? '' : ''}
          </div>
          <div className={styles.search} onClick={handleModal}>
            <SearchOutlinedIcon />
          </div>
        </div>
        <div className={styles.name}>
          <div>{capitalizeFirstLetter(name)}</div>
        </div>
        {pokemon && <MainImg pokemon={pokemon} styles={styles} />}
        <div className={styles.class}>
          {pokemon &&
            pokemon.types.map((e) => (
              <div key={e.type.name} className={styles.className}>
                {capitalizeFirstLetter(e.type.name)}
              </div>
            ))}
        </div>
        <div
          style={{
            width: '95%',
            height: '95%',
            backgroundImage: `url(/img/${background})`,
            backgroundSize: 'cover',
            position: 'absolute',
            borderRadius: 'inherit',
            opacity: '.25',
            zIndex: '-1',
          }}
        ></div>
      </div>
    </>
  );
};

export default CardSample;
