import React, { useEffect, useState } from 'react';
import styles from './MyCards.module.css';
import CloseIcon from '@mui/icons-material/Close';
import { useContent } from '../../userContext';
import useFetch from '../../Hooks/useFetch';
import { POKEMON_GET } from '../../Api';

const MyCards = (name) => {
  const { capitalizeFirstLetter, pokedeck, setPokedeck } = useContent();
  const [pokemon, setPokemon] = useState('');
  const [text, setText] = useState('black');

  const { request } = useFetch();

  useEffect(() => {
    async function getPokemonType() {
      const { url, option } = await POKEMON_GET(name.name);
      const { json } = await request(url, option);
      setPokemon(json);
    }
    getPokemonType();
  }, [name, request]);

  function removePokemon() {
    let newDeck = pokedeck.filter((value) => value !== name.name);
    setPokedeck([...newDeck]);
  }

  const [background, setBackground] = useState('');
  useEffect(() => {
    function changingBackground() {
      switch (pokemon.types[0].type.name) {
        case 'dark':
          setBackground('darkness.png');
          break;
        case 'dragon':
          setBackground('dragon.png');
          break;
        case 'fighting':
          setBackground('fighting.png');
          break;
        case 'fire':
          setBackground('fire.png');
          break;
        case 'grass':
          setBackground('grass.png');
          break;
        case 'electric':
          setBackground('lightning.png');
          break;
        case 'steel':
          setBackground('metal.png');
          break;
        case 'psychic':
          setBackground('psychic.png');
          break;
        case 'water':
          setBackground('water.png');
          break;

        default:
          setBackground('holografic.jpg');
      }
    }
    pokemon && changingBackground();
  }, [pokemon]);
  useEffect(() => {
    background === 'darkness.png' && setText('white');
  }, [background]);

  // console.log(name.name);

  return (
    <>
      {pokemon && (
        <div
          className={styles.container}
          style={{
            color: `${text}`,
            backgroundImage: `url(/img/${background})`,
          }}
        >
          <img src={pokemon.sprites.front_default} alt="" />
          <div className={styles.info}>
            <div className={styles.name}>
              <h1>{capitalizeFirstLetter(pokemon.name)}</h1>
              <h1>{capitalizeFirstLetter(pokemon.types[0].type.name)}</h1>
            </div>
            <div className={styles.abilities}>
              {pokemon.abilities.map((e) => (
                <div
                  key={e.ability.name}
                  style={{ boxShadow: `0 0 3px 1px ${text}` }}
                >
                  {capitalizeFirstLetter(e.ability.name)}
                </div>
              ))}
            </div>
          </div>
          <div className={styles.close} onClick={removePokemon}>
            <CloseIcon />
          </div>
        </div>
      )}
    </>
  );
};

export default MyCards;
