import { useEffect, useState } from 'react';
import { POKEMON_GET } from '../../Api';
import useFetch from '../../Hooks/useFetch';
import styles from './ImgEvolution.module.css';

const ImgEvolution = ({ evoImg }) => {
  const [img, seImg] = useState(null);
  const [pokemonImg, setPokemonImg] = useState(null);

  const { request } = useFetch();
  useEffect(() => {
    if (evoImg) {
      async function getImg() {
        const { url, option } = await POKEMON_GET(evoImg);
        const { json } = await request(url, option);
        seImg(json);
      }
      if (evoImg) getImg();
    }
  }, [evoImg, request]);

  useEffect(() => {
    if (img) {
      img.sprites.other.dream_world.front_default &&
        setPokemonImg(img.sprites.other.dream_world.front_default);
      !img.sprites.other.dream_world.front_default &&
        setPokemonImg(img.sprites.other['official-artwork'].front_default);
    }
  }, [img]);

  // if (evoImg) console.log(img);

  return (
    <>
      <div className={styles.container}>
        {img ? (
          <div>
            <h2 className={styles.stage}>
              STAGE <span>2</span>
            </h2>
            <div className={styles.containerImg}>
              <img
                className={styles.img}
                src={evoImg ? pokemonImg : null}
                alt=""
              />
            </div>
          </div>
        ) : (
          <h2 className={styles.stage2}>BASIC</h2>
        )}
      </div>
    </>
  );
};

export default ImgEvolution;
