import React, { useEffect, useState } from 'react';
import { POKEMON_LIMIT } from '../Api';
import useFetch from '../Hooks/useFetch';
import { useContent } from '../userContext';
import styles from './SearchBar.module.css';

const SearchBar = ({ value, setValue, setName }) => {
  const { capitalizeFirstLetter } = useContent();
  const { request } = useFetch();
  const [pokemons, setPokemons] = useState('');
  const [hidden, setHidden] = useState(false);

  useEffect(() => {
    async function getPokemon() {
      const { url } = await POKEMON_LIMIT(0, 2000);
      const { json } = await request(url);
      setPokemons(json.results);
    }
    getPokemon();
  }, [request, setName]);

  function onChange(input) {
    setValue(input.target.value);
    !hidden && setHidden(true);
  }

  function onSearch(searchTerm) {
    setValue(searchTerm);
    setName(searchTerm);
    setHidden(false);
  }
  function onClick() {
    setName(value);
    setHidden(false);
  }

  return (
    <div className={styles.container}>
      <div className={styles.input}>
        <input
          placeholder="Search pokemon by name"
          value={value}
          className={styles.dropdown}
          onChange={onChange}
        />
        <div
          onClick={() => {
            setValue('');
            setHidden(false);
          }}
          className={styles.clear}
        >
          {value === '' ? ' ' : 'x'}
        </div>
        <button onClick={onClick} className={styles.button}>
          Search
        </button>
      </div>
      {pokemons && hidden && (
        <div className={styles.dropdownMenu}>
          {pokemons
            .filter((name) => {
              const searchTerm = value.toLowerCase();
              const fullName = name.name.toLowerCase();
              return (
                searchTerm &&
                fullName.startsWith(searchTerm) &&
                fullName !== searchTerm
              );
            })
            .map((e) => (
              <p onClick={() => onSearch(e.name)}>
                {' '}
                {capitalizeFirstLetter(e.name)}
              </p>
            ))}
        </div>
      )}
    </div>
  );
};

export default SearchBar;
